<template>
  <div class="dashboard">
      <loading :active.sync="isLoading"></loading>
         <b-nav pills class="mb-3">
          <b-nav-item to="/additem">Add</b-nav-item>
        </b-nav>
        <b-list-group >
          <b-list-group-item v-for="(item, index) in items"  :key="index">{{item.subtitle}}</b-list-group-item>
        </b-list-group>


  </div>
</template>

<script>
import db from './firebaseInit'
import Loading from 'vue-loading-overlay'

export default {
  name: 'Dashboard',
  components: {
    Loading
  },
  data () {
    return {
      
      items: [],
      isLoading: true
    }
  },
   created () {

      // db.collection('sources').onSnapshot(function(querySnapshot) {
      //   querySnapshot.forEach((doc) => {
            
      //       console.log(doc.data());
      //       let data = {
      //         'subtitle': doc.data().name
      //       }
      //       let divider = { divider: true, inset: false }

      //      data.items.push(data)
      //      data.items.push(divider)
           
      //   })
      // })
    db.collection('sources').get().then((querySnapshot) => {
      this.loading = false

      querySnapshot.forEach((doc) => {
        
          let data = {
            'subtitle': doc.data().name
          }

        this.items.push(data)

        this.isLoading = false 
      })

    })


  }
}
</script>



